*::before,
*::after,
*{
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html{
  font-size: 62.5%;
}

body {
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
    background: #03011e;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.particles {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
}
.data {
    border: 1px solid red;
    width: 25%;
    height: auto;
    padding: .75rem 1rem;
    margin: 1rem 2rem;
    color: #fff;
    text-align: center;
    
}
.container {
    max-width: 1100px;
    margin:  15vh auto 0;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
}
.search:focus,
.search:active{
    outline: none;
}

.search{
    border: 1px solid #ecc860;
    border-radius: 3px;
    padding: .75rem 1rem;
}
@font-face {font-family:"STARWARS";src:url(/swapiZerotoMastery/static/media/STARWARS.fa7a31d2.eot) format("eot"),url(/swapiZerotoMastery/static/media/STARWARS.35f31c7a.woff) format("woff"),url(/swapiZerotoMastery/static/media/STARWARS.3f085be9.ttf) format("truetype"),url(/swapiZerotoMastery/static/media/STARWARS.95e43dd6.svg#STARWARS) format("svg");font-weight:normal;font-style:normal;}



.header{
    background: whitesmoke;
    width: 100vw;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    padding: .75rem 2.5rem;
    border-bottom: 1px solid #000;
    position: fixed;
    top: 0;
    left: 0;
}

.logo {
    font-family: "STARWARS";
}
